.device-selector {
    margin-top: 40px;
    overflow: hidden;
}

.device-selector-container {
    display: flex;
    justify-content: center;
    padding: 20px 0;
}

.device-selector dl {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(320px, 1fr));
    grid-template-rows: repeat(auto-fill, 1fr);
    gap: 10px;
}

.device-selector dl dt {
    font-size: 20px;
    font-weight: 600;
    letter-spacing: 0;
    font-style: normal;
    padding: 10px 0;
    grid-column: 1 / -1;
    display: flex;
    justify-content: space-between;
}

.device-selector dl dd {
    padding: 0;
    margin: 0;
    position: relative;
    cursor: pointer;
    text-align: left;
    flex-shrink: 0;
    flex-grow: 1;
}

.device-selector dl dd input {
    position: absolute;
    width: 100%;
    height: 100%;
    appearance: none;
    top: 0;
    z-index: -1;
}
.device-selector dl dd label {
    display: grid;
    grid-template-columns: 1fr 1fr;
    width: 100%;
    height: 100%;
    padding: 20px;
    text-align: center;
    border: 2px solid #d6d6d6;
    box-sizing: border-box;
    border-spacing: 0;
    overflow: hidden;
    cursor: pointer;
    background-color: hsla(0,0%,100%,.8);
    flex-direction: column;
    cursor: pointer;
    transition: all 0.5s ease;
}
.device-selector dl dd.active label, .device-selector dl dd.hover label {
    border-color: #0070c9;
}

.device-selector dl dd label span, .device-selector dl dt span {
    display: flex;
    align-items: center;
    justify-content: center;
    color: black;
    background: #e9ecef;
    font-size: 12px;
    text-transform: uppercase;
    font-weight: 600;
    letter-spacing: 2px;
    padding: 10px 20px;
    border-radius: 30px;
    cursor: pointer;
    text-transform: uppercase;
    transition: all 0.5s ease;
    opacity: 0.8;
    transform: scale(0.8);
}
.device-selector dl dd label span.active, .device-selector dl dt span.active {
    opacity: 1;
    transform: scale(1);
}
.device-selector dl dd label div {
    display: flex;
    flex-direction: column;
    align-items: center;
}
.device-selector dl dd label div p {
    font-weight: 600;
    font-size: 17px;
}
.device-selector dl dd label ul {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    list-style: none;
    justify-content: flex-start;
}
.device-selector dl dd label ul li {
    display: block;
    height: 24px;
    width: 24px;
    margin: 0 8px 8px 0;
    border-radius: 50%;
    transition: all 0.5s ease;
    box-shadow: inset 0 0 0 1px rgba(0, 0, 0, 0.1);
    transform: scale(0.8);
}
.device-selector dl dd label ul li.active {
    box-shadow: inset 0 0 0 1px rgba(0, 0, 0, 0.3);
    transform: scale(1);
}
.device-selector dl dd label ul li.black {
    background-color: #2c2b2c;
}
.device-selector dl dd label ul li.silver {
    background-color: #f0f0f0;
}
.device-selector dl dd label ul li.gold {
    background-color: #f1e2d0;
}
.device-selector dl dd label ul li.white {
    background-color: #fff;
}
.device-selector dl dd label ul li.red {
    background-color: #f96b6c;
}
.device-selector dl dd label ul li.yellow {
    background-color: #f2dc60;
}
.device-selector dl dd label ul li.green {
    background-color: #97e563;
}
.device-selector dl dd label ul li.blue {
    background-color: #33a2db;
}
